.box{
   width: 90%;
   height: 500px;
   background: rgb(68, 68, 68);
   color: white;
   margin: 100px 5%
}
.box img{
    display: block;
    width: 600px;
    height: 400px;
    float: left;
    margin-left: 20px;
}
.content{
    width: 500px;
    height: 400px;
    float: left;
    margin-left: 20px;
}
.content p:first-child{
    text-indent: 2em;
}
.box h1{
    font-size: 24px;
    line-height: 68px;
    color: white
}
.box span{
    font-weight: bold;
}
.tabs{
    background: rgb(68, 68, 68);
    padding-left: 100px;
    color: white
}
.tab{
    background: rgb(68, 68, 68);
}