* {padding: 0;margin: 0;}
			
			.Box {position: relative;}
			.Box .content {width: 90%;margin: 50px;background: #515151}
			.Box h2 {text-align: center;margin-bottom: 35px;padding-top: 250px;}
			.Box .Box_con {position: relative;}
			.Box .Box_con .btnl {position: absolute;}
			.Box .Box_con .btn {display: block;width: 41px;height: 41px;position: absolute;top: 80px;cursor: pointer;}
			.Box .Box_con .btnl {background: url("http://www.ttpcn.com/uploads/2019/0115/37db094106c351b528908d779ec27db5.jpg") no-repeat center;left: -72px;}
			.Box .Box_con .btnr {background: url("http://www.ttpcn.com/uploads/2019/0115/37db094106c351b528908d779ec27db5.jpg") no-repeat center;right: -72px;}
			.Box .Box_con .btnl:hover {background: url("http://www.ttpcn.com/uploads/2019/0115/37db094106c351b528908d779ec27db5.jpg") no-repeat center;}
			.Box .Box_con .btnr:hover {background: url("http://www.ttpcn.com/uploads/2019/0115/37db094106c351b528908d779ec27db5.jpg") no-repeat center;}
			.Box .Box_con .conbox {position: relative;overflow: hidden;}
			.Box .Box_con .conbox ul {position: relative;list-style: none;}
			.Box .Box_con .conbox ul li {float: left;width: 285px;height: 350px;margin-left: 20px;overflow: hidden;}
			.Box .Box_con .conbox ul li:first-child {margin-left: 0;}
			.Box .Box_con .conbox ul li img {display: block;width: 285px;height: 350px;transition: all 0.5s;}
			.Box .Box_con .conbox ul li:hover img {transform: scale(1.1);}
			
			.Box .BoxSwitch {margin-top: 30px;text-align: center;}
			.Box .BoxSwitch span {display: inline-block;*display: inline;*zoom: 1;vertical-align: middle;width: 30px;height: 3px;background: #ccc;margin: 0 5px;cursor: pointer;}
